import React from 'react';
import { motion } from 'framer-motion';
import { TrendingUp, TrendingDown, Clock, CheckCircle, XCircle } from 'lucide-react';

interface SignalResult {
    id: string | number;
    pair: string;
    type: string; // DEAL_TYPE_BUY or DEAL_TYPE_SELL
    entryPrice: string;
    closePrice: string;
    netProfit: number;
    pips: number;
    lotSize: number;
    timestamp: string;
    provider: string;
}

interface ResultCardProps {
    result: SignalResult;
}

const ResultCard: React.FC<ResultCardProps> = ({ result }) => {
    const isProfit = result.netProfit >= 0;
    const isBuy = result.type.includes('BUY');

    // Format profit string
    const profitString = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: 2
    }).format(result.netProfit);

    return (
        <motion.div
            className={`signal-card result-card ${isProfit ? 'profit' : 'loss'}`}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            whileHover={{ scale: 1.02 }}
            layout
        >
            <div className="card-header">
                <div className="pair-info">
                    <span className="pair-name">{result.pair}</span>
                    <span className={`signal-type ${isBuy ? 'buy' : 'sell'}`}>
                        {isBuy ? 'BUY' : 'SELL'}
                    </span>
                </div>
                <div className="provider-info">
                    <span className="provider-rank">Verstige AI</span>
                </div>
            </div>

            <div className="card-body">
                <div className="price-row">
                    <div className="price-item">
                        <span className="label">ENTRY</span>
                        <span className="value">{result.entryPrice}</span>
                    </div>
                    <div className="arrow">→</div>
                    <div className="price-item">
                        <span className="label">EXIT</span>
                        <span className="value">{result.closePrice}</span>
                    </div>
                </div>

                <div className="result-row">
                    <div className="pnl-container">
                        <span className="pnl-label">Net Profit</span>
                        <span className={`pnl-value ${isProfit ? 'green' : 'red'}`}>
                            {isProfit ? '+' : ''}{profitString}
                        </span>
                    </div>
                    <div className={`status-icon ${isProfit ? 'win' : 'loss'}`}>
                        {isProfit ? <TrendingUp size={24} /> : <TrendingDown size={24} />}
                    </div>
                </div>
            </div>

            <div className="card-footer">
                <div className="meta-info">
                    <Clock size={12} />
                    <span>{new Date(result.timestamp).toLocaleTimeString()}</span>
                </div>
                <div className="lot-info">
                    <span>{result.lotSize} Lots</span>
                </div>
            </div>
        </motion.div>
    );
};

export default ResultCard;
