                    setAccountEquity(data.equity || (data.balance * 1.02) || 0);
                    setMarginUsed(data.margin_used || 0);
                    setFreeMargin(data.free_margin || 0);
                    setUnrealizedPnl(data.unrealized_pnl || 0);

                    // Set analytics from API
